	module.exports = {"data":{"site":{"siteMetadata":{"author":"Burak Tokak","description":"SaaS, e-ticaret ve diğer dijital ürünlerinizin verimini arttırmak için kullanabileceğiniz ücretsiz ve örneklerle destekli bir psikolojik prensip kütüphanesidir.","facebookAppId":"310591556081648","title":"Dijital Psikoloji","twitterHandle":"danistefanovic","siteUrl":"https://dijitalpsikoloji.com"}},"markdownRemark":{"html":"<p>İnsanlar olarak hiçbir şeyi yarım bırakmayı sevmeyiz. Sonunda bir ödül olmasa bile, sadece bitirmenin verdiği haz için, bir grup görevi tamamlarız.</p>\n<h2>Studies</h2>\n<p>During the 2016 Christmas season, Harvard researchers <a href=\"http://www.hbs.edu/faculty/Pages/item.aspx?num=52665\">Barasz, John, Keenan and Norton (2017)</a> conducted a study with the Canadian Red Cross. Potential donors were directed to one of three landing pages. The first group received a request for cash donations. The second group was asked to select gifts from a selection of six items, like blankets, baby diapers, or hot meals, among others. A location marker on a globe indicated where the selected items would be donated. The third landing page offered the same gifts, but in a pseudo-set called a “Global Survival Kit.” Each time an item was selected, instead of location markers, a line grew further around the globe. A closed circle meant that all six items were funded, but potential donors could choose as many gifts as they wanted. 21% of the people in the third group decided to donate all six gifts, compared with only 5% on the second page, and 3% in the cash condition.</p>\n<p>We like to tick things off that we gotten done. In the days before paper and pen, our memories had to perform the whole job. To keep track, our mind fixates on unfinished tasks – the so-called <em>Zeigarnik effect</em>. The psychologist Bluma Zeigarnik had noted that waiters could remember open orders more efficiently than those that had been delivered. Further experiments have proven that we can recall incomplete or interrupted things better than things we’ve finished (<a href=\"http://codeblab.com/wp-content/uploads/2009/12/On-Finished-and-Unfinished-Tasks.pdf\">Zeigarnik, 1927</a>).</p>\n<h2>Examples</h2>\n<h3>Wizards</h3>\n<p>No, I don’t mean those bearded guys with hats. We’re talking about the step-by-step process that takes the user through a sequence of input requests. It’s a popular design pattern to split up complex forms into smaller, more digestible, tasks. Display either a progress bar or the number of steps that are left to indicate to users how long the whole process will take. An indicator will also act as a reminder of how much time they’ve already invested – time that <a href=\"/loss-aversion/\">would be lost</a> should they not complete the process.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 550px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 56.333333333333336%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAALCAYAAAB/Ca1DAAAACXBIWXMAAAsSAAALEgHS3X78AAAAhklEQVQoz2NgGAVUBQcOHGDMyMjgKiwsZK+rq2OBiRcVFXHk5eWxZ2ZmgsXq6+tZQGLFxcVseA3cvn07Y1pamkh6erp0dna2MEwcKCaRkpIiCTSAHcrnAFosnZuby0PIkQJArADEckCsAsSqUFpJQkJCDGiRINAiASjNBzRYGMjmGI1b6gIAuZElUCvMpbAAAAAASUVORK5CYII='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Need to Complete Example: Wizards\"\n        title=\"\"\n        src=\"/static/4e31aa2440660388016f0da846b7f15e-bf077.png\"\n        srcset=\"/static/4e31aa2440660388016f0da846b7f15e-235b1.png 138w,\n/static/4e31aa2440660388016f0da846b7f15e-730c0.png 275w,\n/static/4e31aa2440660388016f0da846b7f15e-bf077.png 550w,\n/static/4e31aa2440660388016f0da846b7f15e-b4be9.png 825w,\n/static/4e31aa2440660388016f0da846b7f15e-8a14d.png 1100w,\n/static/4e31aa2440660388016f0da846b7f15e-9dd46.png 1200w\"\n        sizes=\"(max-width: 550px) 100vw, 550px\"\n      />\n    </span>\n  </span>\n  </p>\n<h3>Progress bars</h3>\n<p>People <a href=\"http://dl.acm.org/citation.cfm?id=317459\">want</a> progress indicators. They give feedback about the current status, but also stimulate the desire to continue the process. Each time the progress bar moves through our actions, we feel the positive effects of dopamine, which motivates us to repeat the previous task. This phenomenon is also referred to as <em>self-directed learning</em>, which is likely evolutionarily developed to maximize learning opportunities and, therefore, to reproduce human intelligence (<a href=\"https://grey.colorado.edu/mediawiki/sites/mingus/images/c/c0/HerdMingusOReilly10.pdf\">Herd, Mingus &#x26; O’Reilly, 2010</a>).</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 550px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 56.333333333333336%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAALCAYAAAB/Ca1DAAAACXBIWXMAAAsSAAALEgHS3X78AAAASElEQVQoz2NgGAU0B+np6fJpaWkqGRkZICxLsYFZWVmiQIofiAWhbMrA6dOnuXbt2sUBdKU20IUKVPN6VVUVS25uLtNoIsAAAFibEjP15Xp+AAAAAElFTkSuQmCC'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Need to Complete Example: Progress bars\"\n        title=\"\"\n        src=\"/static/0f6f03e527d6196b7dab17eb0b06e5ab-bf077.png\"\n        srcset=\"/static/0f6f03e527d6196b7dab17eb0b06e5ab-235b1.png 138w,\n/static/0f6f03e527d6196b7dab17eb0b06e5ab-730c0.png 275w,\n/static/0f6f03e527d6196b7dab17eb0b06e5ab-bf077.png 550w,\n/static/0f6f03e527d6196b7dab17eb0b06e5ab-b4be9.png 825w,\n/static/0f6f03e527d6196b7dab17eb0b06e5ab-8a14d.png 1100w,\n/static/0f6f03e527d6196b7dab17eb0b06e5ab-9dd46.png 1200w\"\n        sizes=\"(max-width: 550px) 100vw, 550px\"\n      />\n    </span>\n  </span>\n  </p>\n<h3>Starting with a high percentage</h3>\n<p>Make your progress indicator prominent, and show after the first completed task a relatively high completion rate. But, don’t overdo it. Keep it plausible. We are more motivated to reach a goal the closer we get to it (<a href=\"http://www.quilageo.com/wp-content/uploads/2013/07/Goal-Gradient_Illusionary_Goal_Progress.pdf\">Kivetz, Urminsky &#x26; Zheng, 2006</a>).</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 550px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 56.333333333333336%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAALCAYAAAB/Ca1DAAAACXBIWXMAAAsSAAALEgHS3X78AAAAaElEQVQoz2NgGAXI4Pjx47xPnz5lgfH37dvHvn//fkGyDdy7dy/fzZs3mWH8U6dOcYDEKHZpXl4ea1ZWFjvFBiUkJDABKUYoZnBwcAC5lolsA2VlZSWBlBIQq4H4np6eXEBKZjR1oQAAnvsZWY/xtucAAAAASUVORK5CYII='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Need to Complete Example: Starting with a high percentage\"\n        title=\"\"\n        src=\"/static/2f87af8b5314a0cdc4d689adba1f497a-bf077.png\"\n        srcset=\"/static/2f87af8b5314a0cdc4d689adba1f497a-235b1.png 138w,\n/static/2f87af8b5314a0cdc4d689adba1f497a-730c0.png 275w,\n/static/2f87af8b5314a0cdc4d689adba1f497a-bf077.png 550w,\n/static/2f87af8b5314a0cdc4d689adba1f497a-b4be9.png 825w,\n/static/2f87af8b5314a0cdc4d689adba1f497a-8a14d.png 1100w,\n/static/2f87af8b5314a0cdc4d689adba1f497a-9dd46.png 1200w\"\n        sizes=\"(max-width: 550px) 100vw, 550px\"\n      />\n    </span>\n  </span>\n  </p>\n<h3>Counters and notifications</h3>\n<p>You might have heard about the <a href=\"https://www.youtube.com/watch?v=z9UjeTMb3Yk\">Inbox Zero</a> approach: a technique developed by Merlin Mann to keep your email inbox empty almost all the time. For many people, counters, especially those that indicate incomplete tasks, trigger discomfort, which we try to avoid. According to a study from 2002, employees reacted to 70% of their unread emails <a href=\"https://dspace.lboro.ac.uk/dspace-jspui/bitstream/2134/489/3/Ease%2525202002%252520Jackson.pdf\">within 6 seconds</a>. These interruptions can be instant gratification. They give us little dopamine rushes every time we complete something, which in turn, <a href=\"/rewards/\">reinforces us</a> to lurk for more distractions.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 550px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 56.333333333333336%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAALCAYAAAB/Ca1DAAAACXBIWXMAAAsSAAALEgHS3X78AAAAIElEQVQoz2NgGAVDD2RmZkqkpaXJAplCQCw2GiJDEAAA6NECy0KW4T8AAAAASUVORK5CYII='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Need to Complete Example: Counters and notifications\"\n        title=\"\"\n        src=\"/static/c1dd222be7704d4aebcff36176e9e539-bf077.png\"\n        srcset=\"/static/c1dd222be7704d4aebcff36176e9e539-235b1.png 138w,\n/static/c1dd222be7704d4aebcff36176e9e539-730c0.png 275w,\n/static/c1dd222be7704d4aebcff36176e9e539-bf077.png 550w,\n/static/c1dd222be7704d4aebcff36176e9e539-b4be9.png 825w,\n/static/c1dd222be7704d4aebcff36176e9e539-8a14d.png 1100w,\n/static/c1dd222be7704d4aebcff36176e9e539-9dd46.png 1200w\"\n        sizes=\"(max-width: 550px) 100vw, 550px\"\n      />\n    </span>\n  </span>\n  </p>\n<h3>To-do lists</h3>\n<p>Dropbox has an excellent “Get Started” checklist. The user is asked to refer friends to them, to connect social media accounts, and to follow Dropbox on Twitter, which resulted in more free space. This growth hack is undoubtedly one reason why Dropbox became a multi-billion-dollar company. Providing small, achievable quests can produce small wins when completed. But, not all users will be seduced solely by the need to complete. In this case, give them extrinsic rewards, as Dropbox does.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 550px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 56.333333333333336%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Need to Complete Example: To-do lists\"\n        title=\"\"\n        src=\"/static/60990f3fa42dec44dde961e070dd5b5a-bf077.png\"\n        srcset=\"/static/60990f3fa42dec44dde961e070dd5b5a-235b1.png 138w,\n/static/60990f3fa42dec44dde961e070dd5b5a-730c0.png 275w,\n/static/60990f3fa42dec44dde961e070dd5b5a-bf077.png 550w,\n/static/60990f3fa42dec44dde961e070dd5b5a-b4be9.png 825w,\n/static/60990f3fa42dec44dde961e070dd5b5a-8a14d.png 1100w,\n/static/60990f3fa42dec44dde961e070dd5b5a-9dd46.png 1200w\"\n        sizes=\"(max-width: 550px) 100vw, 550px\"\n      />\n    </span>\n  </span>\n  </p>","fields":{"path":"/tamamlama-ihtiyaci/","slug":"tamamlama-ihtiyaci"},"frontmatter":{"datePublished":"2017-09-11T08:15:15Z","title":"Tamamlama İhtiyacı","seoImage":{"childImageSharp":{"resize":{"src":"/static/cb497f3460653fa0c73b85de34f51ac6-a159b.png"}}}}},"allMarkdownRemark":{"edges":[{"node":{"fields":{"path":"/adanma-sureklilik/","slug":"adanma-sureklilik"},"frontmatter":{"title":"Adanma ve Süreklilik"}}},{"node":{"fields":{"path":"/az-bulunurluk/","slug":"az-bulunurluk"},"frontmatter":{"title":"Az Bulunurluk"}}},{"node":{"fields":{"path":"/capalanmislik/","slug":"capalanmislik"},"frontmatter":{"title":"Çapalanmışlık"}}},{"node":{"fields":{"path":"/kayip-kahri/","slug":"kayip-kahri"},"frontmatter":{"title":"Kayıp Kahrı"}}},{"node":{"fields":{"path":"/mutekabiliyet/","slug":"mutekabiliyet"},"frontmatter":{"title":"Mütekabiliyet"}}},{"node":{"fields":{"path":"/odullendirme/","slug":"odullendirme"},"frontmatter":{"title":"Ödüllendirme"}}},{"node":{"fields":{"path":"/sosyal-dayanak/","slug":"sosyal-dayanak"},"frontmatter":{"title":"Sosyal Dayanak"}}},{"node":{"fields":{"path":"/tamamlama-ihtiyaci/","slug":"tamamlama-ihtiyaci"},"frontmatter":{"title":"Tamamlama İhtiyacı"}}},{"node":{"fields":{"path":"/tetikleyiciler/","slug":"tetikleyiciler"},"frontmatter":{"title":"Tetikleyiciler"}}}]}},"pathContext":{"path":"/tamamlama-ihtiyaci/","slug":"tamamlama-ihtiyaci"}}

